* {
  outline: none;
  box-sizing: border-box;
}


.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 10px;
}

.search-section {
  flex-basis: 30%;
  position: relative;

}

.search-section input {
  width: 100%;
  height: 40px;
  border-radius: 25px;
  padding: 0 30px;
  border: 1px solid #dedede;
  
}

.search-icon {
  position: absolute;
  right: 10px;
  top: 8px;
  font-size: 24px;
  opacity: .7;
  cursor: pointer;

}

.nav-links {
  flex-basis:40%;
  display: flex;
  justify-content: space-around;
  font-size: 28px;
 
}

.nav-links a {
  text-decoration: none;
  color: #000;
  border-bottom: 1px solid #ddd;
}

.nav-links a:first-child {
  color: red;
}

.tel {
  font-size: 24px;
  font-family: Arial, Helvetica, sans-serif;
}



.overlay {
  transform:scale(0);
  transition: scale 1s ease;

  

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0,0,0,.7);
  z-index: 9999;
  color: #ddd;
  font-size: 27px;
}

.qerar-qebulu {
  display: flex;
  gap: 20px;
}

.qerar-qebulu button {
  padding: 10px 40px;
  background-color: transparent;
  border: 1px solid white;
  font-size: 36px;
  color: #fff;
  cursor: pointer;
}

.qerar-qebulu button:hover {
  background-color: white;
  color: #000; 
  transition:  .7s ease-in;
  box-shadow: 5px 27px 35px rgba(0,0,0,.2);
}

.active {
  transform: scale(1);

  transform: scale 2s ease-in;
}


/* products komponenti stilleri */

.mehsullar {
  padding:12px 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.product {
  width: 20%;
  min-width: 20px;
  box-shadow: 5px 5px 10px rgba(0,0,0,.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  margin: 5px;
}

.product:hover {
  transform: scale(1.01);
  transition: .8s ease-in;
}

.product img {
  width: 250px;
}

.refund-btn {
  padding: 10px 30px;
  background-color: transparent;
  border: 1px solid #dedede;
}

.add-basket-btn {
  padding: 10px 30px;
  border: none;
  background-color: transparent;
  border: 1px solid #dedede;

}


.text {
  font-size: 37px;
}






.swiper-wrapper {
  height: 300px;
}



/* BASKET VE BASKET CONTAİNER UCUN STİLLER */

.basket-container {

text-align: center;
box-shadow: 6px 5px 8px rgba(0,0,0,.8);
width: 500px;
margin: 0 auto;
padding: 30px;

}

.basket-item ul {
  list-style-type: none;
}




